var aws_sdk = require('aws-sdk');

function _getClient(accessKey, secretKey)
{
    return new aws_sdk.S3({
        apiVersion: '2006-03-01',
        accessKeyId: accessKey,
        secretAccessKey: secretKey,
        region: 'us-east-2'
    });
}

module.exports = {
    getClient: _getClient
};